<template>
  <div id="credit-requests-list">
    <v-layout row fill-height>
      <v-flex fill-height md6 class="mx-auto">
        <v-card class="my-4">
          <v-toolbar flat dense height="72">
            <v-toolbar-title class="subtitle font-weight-bold grey--text pl-3">Pengajuan Kredit</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-btn icon dark flat class="prim-1-text" to="/app/credit-requests/add">
              <v-icon>add</v-icon>
            </v-btn>
          </v-toolbar>
          <v-divider/>
          <v-list>
            <template v-for="item in items">
              <v-list-tile :key="`cr-list-${item.id}`" avatar class="pl-4">
                <v-list-tile-avatar>
                  <text-avatar :x="item.member.nama[0]" :size="40"></text-avatar>
                </v-list-tile-avatar>
                <v-list-tile-content>
                  <v-list-tile-title>{{ item.member.nama }}</v-list-tile-title>
                  <v-list-tile-sub-title>{{ item.base.created }}</v-list-tile-sub-title>
                </v-list-tile-content>
                <v-list-tile-action>
                  <div>
                    <v-menu offset-y>
                      <template v-slot:activator="{ on }">
                        <v-btn small icon v-on="on" class="my-0">
                          <font-awesome-icon icon="ellipsis-h"></font-awesome-icon>
                        </v-btn>
                      </template>
                      <v-list dense>
                        <v-list-tile :to="`/app/credit-requests/${item.id}/detail`">
                          <v-list-tile-sub-title>Detail</v-list-tile-sub-title>
                        </v-list-tile>
                        <v-list-tile :to="`/app/credit-requests/${item.id}/edit`">
                          <v-list-tile-sub-title>Edit</v-list-tile-sub-title>
                        </v-list-tile>
                        <v-list-tile @click="remove(item.id)">
                          <v-list-tile-sub-title>Hapus</v-list-tile-sub-title>
                        </v-list-tile>
                      </v-list>
                    </v-menu>
                  </div>
                </v-list-tile-action>
              </v-list-tile>
              <v-divider :key="`cr-sep-${item.id}`"></v-divider>
            </template>
          </v-list>
        </v-card>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
import { CreditRequest } from '@/services'
import Actions from '@/store/actions'

export default {
  name: 'CreditRequestList',
  data: () => ({
    items: []
  }),
  methods: {
    async loadData () {
      this.$store.commit('loading', true)
      try {
        const items = await CreditRequest.list()
        this.items = items
      } finally {
        this.$store.commit('loading', false)
      }
    },
    async remove (id) {
      this.$store.dispatch(Actions.CREDIT_REQUEST_REMOVE, id)
        .then(() => {
          this.loadData()
        })
    }
  },
  mounted () {
    this.loadData()
  }
}
</script>

<style lang="scss">
.no-marg {
  .v-input__slot {
    margin: 0;
  }
}
</style>
